@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;600&display=swap);
.card {
  height: 200px;
  width: 400px;
  border-radius: 10px;
  background: rgb(154, 192, 241);
  /* Position */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* Box Shadow */
  box-shadow: 0px -2px 5px rgba(255, 255, 255, 0.3),
    -5px -5px 10px rgba(0, 0, 0, 0.1) inset;
  margin: 25px;
}

.name {
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 2px;
  color: rgba(0, 0, 0, 0.75);
  margin: 10px 0px;
}
.value {
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 2px;
  color: rgba(0, 0, 0, 0.5);
  margin: 10px 0px;
}

.delete-btn {
  margin: 20px;
  padding: 10px 15px;
  background: red;
  border: 2px solid rgba(255, 255, 255, 0.473);
  color: aliceblue;
  border-radius: 5px;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
}

.delete-btn:active {
  transform: translatey(1px);
}

.content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.content-box {
  padding: 0px 10px;
  height: 500px;
  width: auto;
  overflow-y: scroll;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
    font-family: 'Montserrat', sans-serif;
}
